.hero-bg-video {
	overflow: hidden;
	position: relative;
	height: 100vh;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;

	&:after {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		background: linear-gradient(180deg,
			rgba(27, 32, 36, 0.68) 0%,
			rgba(22, 26, 29, 0.23) 100%);
	}

	.container {
		position: relative;
		z-index: 1;
		padding: 50px;
	}

	h2 {
		color: white;
		line-height: 1;
		margin: 0 0 0.27em;
	}

	p {
		margin: 0 0 2.6rem;
	}

	.text-area {
		color: white;
		font-size: 20px;
		line-height: 1.2;
	}
}