.dl-container {
	overflow: hidden;
}
.anim-target {
	display: none;
}

.fade-enter {
	opacity: 0;
	transform: translateX(-100px);
}

.fade-enter-active {
	transition: opacity 1s, transform 1s;
}

.fade-enter-to {}

.fade-leave {}

.fade-leave-active {
	transition: opacity 1s, transform 1s;
}

.fade-leave-to {
	opacity: 0;
	transform: translateX(100px);
}