.resize-active {
	transition: none !important;

	*,
	*:before,
	*:after {
		transition: inherit !important;
	}
}

.header-menu-wrapper {
	padding: 15px 0;
	position: relative;
	background: #ccc;

	.container {
		display: flex;
		justify-content: space-between;
		align-items: flex-end;

		@include media("<=tablet") {
			align-items: center;
		}
	}
}

.logo {
	width: 100px;
	height: 50px;
	background: $black;
	color: $white;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 5px;
}

.nav-opener {
	display: none;
	width: 40px;
	height: 40px;
	background: $black;
	position: relative;
	border-radius: 3px;

	@include media("<=tablet") {
		display: block;
	}

	&:before,
	&:after,
	span {
		background: #fff;
		border-radius: 4px;
		position: absolute;
		top: 50%;
		left: 20%;
		right: 20%;
		height: 4px;
		margin-top: -2px;
		transition: all 0.2s linear;
	}

	&:before,
	&:after {
		content: "";
		top: 30%;
	}

	&:after {
		top: 70%;
	}

	&:hover {
		opacity: .9;
	}
}

.nav-drop {
	transition: all 0.25s linear;

	@include media("<=tablet") {
		position: absolute;
		top: 100%;
		left: 0;
		width: 100%;
		overflow: hidden;
		max-height: 0;
	}
}

.nav-active {
	.nav-opener {
		span {
			opacity: 0;
		}

		&:before,
		&:after {
			transform: rotate(45deg);
			top: 50%;
			left: 15%;
			right: 15%;
		}

		&:after {
			transform: rotate(-45deg);
		}
	}

	.nav-drop {
		@include media("<=tablet") {
			max-height: 4000px;
		}
	}

	#nav {
		@include media("<=tablet") {
			transform: translateY(0);
		}
	}
}

#nav {
	transition: all 0.25s linear;

	@include media("<=tablet") {
		transform: translateY(-100%);
		background: $black;
	}

	ul {
		@extend %listreset;
		display: flex;
		color: $black;

		@include media("<=tablet") {
			display: block;
			color: $white;
		}
	}

	a {
		display: block;
		position: relative;
		padding: 10px;
		color: inherit;

		&:hover,
		&.highlighted {
			background: $gray;
			color: $white;
		}

		&.highlighted {
			.sub-arrow {
				&:before {
					@include media("<=tablet") {
						content: "-";
					}
				}
			}
		}

		&.has-submenu {
			padding-right: 25px;
		}

		.sub-arrow {
			position: absolute;
			top: 50%;
			right: 0;
			width: 0;
			height: 0;
			margin: 0 7px;
			border-width: 5px;
			transform: translateY(-50%);
			border-style: solid dashed dashed dashed;
			border-color: $black transparent transparent transparent;

			@include media("<=tablet") {
				width: 25px;
				height: 25px;
				overflow: hidden;
				border: none;
				font: bold 16px/34px monospace !important;
				text-align: center;
				text-shadow: none;
				background: $white;
				border-radius: 4px;
				color: $black;
				display: flex;
				justify-content: center;
				align-items: center;
			}

			&:before {
				@include media("<=tablet") {
					content: "+";
				}
			}
		}
	}

	.has-mega-menu {
		position: static !important;

		.mega-menu {
			margin-left: 0 !important;
			right: 0 !important;
			width: auto !important;
			max-width: none !important;

			li {
				display: flex;

				@include media("<=tablet") {
					display: block;
				}
			}

			.col {
				width: 33.33%;
				padding: 15px;

				@include media("<=tablet") {
					width: 100%;
				}

				ul {
					position: static;
					display: block;
					margin: 0;
					padding: 0 0 0 20px;
					background: transparent;
				}

				li {
					color: $white;
					display: list-item;
					list-style-type: disc;
					margin: 0 0 0.5em;
				}

				a {
					color: inherit;
					display: inline;
					padding: 0;

					&:hover {
						background: transparent;
						color: blue;
					}
				}
			}
		}
	}

	li {
		position: relative;
		padding: 0;
		margin: 0;

		&:after,
		&:before {
			display: none;
		}

		>ul {
			position: absolute;
			display: none;
			background: green;
			color: $white;

			@include media("<=tablet") {
				position: static;
			}

			ul {
				background: blue;

				ul {
					background: red;
				}
			}

			a {
				color: inherit;
			}

			.sub-arrow {
				border-style: dashed dashed dashed solid;
				border-color: transparent transparent transparent $white;
			}
		}
	}
}