.count-container {
	position: relative;
	padding-bottom: 15px;
}
#count-output {
	font-size: 30px;
}
#count-slider {
	position: absolute;
	height: 8px;
	top: calc(100% - 8px);
	left: 0;
	background: linear-gradient(to right, red 15%, orange 25%, yellow 35%, green 50%, blue 65%, indigo 80%, violet 100%);
	width: 100%;
}