.circle-progess-block {
	display: inline-block;
	position: relative;
	background-color: tomato;
	padding: 7px;
	border-radius: 50%;
	font-size: 0;
}

.circle-progess-block-blue {
	background-color: blue;
	.circle-animation-progress {
		color: #fff;
	}
}

.circle-animation-progress {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	color: #000000;
	font-size: 16px;
	font-weight: 700;
}