.snap-svg {
	max-width: 600px;
	width: 100%;
	height: 300px;
	background-color: tomato;
}

.snap-svg-block2 {
	svg {
		background-color: pink;
	}
}

.snap-svg-bloc3 {
	@include aspect-ratio(16,4,"svg");
	#waves {
		// position: fixed;
		// top: 0;
		// left: 0;
		// width: 100vw;
		// height: 30vh;
	}

	.waves__01 {
		fill: #fec300;
	}

	.waves__02 {
		fill: #bccf3c;
	}

	.waves__03 {
		fill: #fff;
	}

	.waves__1 {
		fill: #f2f2c8;
	}

	.waves__2 {
		stroke: #d8d7b1;
		fill: transparent;
	}

	.waves__3 {
		stroke: #dad9b3;
		stroke-width: 0.5;
		fill: transparent;
		stroke-dasharray: 3;
	}
}